/* 0. Reset ================================= */
@import 'reset';
@import url('https://fonts.googleapis.com/css?family=Alegreya|Montserrat|Princess+Sofia|Gloria+Hallelujah|Raleway');

$headerText: "Alegreya";
$bodyText: "Montserrat";
$linkText: "Raleway";
$headerColor: #51A7AD;
$wrapperColor: #F7A7BF;
// $wrapperColor: #F5ABB4;
$backgroundColor: #F1EDE3;

@keyframes ani-move-right{
	0% {
		transform: translateX(0%);
	}
	20%{
		transform: translateX(-25%);
	}
	30%{
		transform: translateX(25%);
	}
	40%{
		transform: translateX(50%);
	}
	50%{
		transform: translateX(0%);
	}	
	60%{
		transform: translateX(25%);
	}
	70%{
		transform: translateX(50%);
	}
	80%{
		transform: translateX(0%);
	}	
}

@keyframes ani-move-left{
	0% {
		transform: translateX(0%);
	}
	20%{
		transform: translateX(25%);
	}
	30%{
		transform: translateX(-25%);
	}
	40%{
		transform: translateX(-50%);
	}
	50%{
		transform: translateX(0%);
	}	
	60%{
		transform: translateX(-25%);
	}
	70%{
		transform: translateX(-50%);
	}
	70%{
		transform: translateX(-50%);
	}
	80%{
		transform: translateX(0%);
	}	
}



/* 1. Main Layout ================================= */

* {
	box-sizing: border-box;
}

body {
	background: $backgroundColor;
}

.wrapper{
	width: 80%;	
	background: $wrapperColor;
	margin: 0 auto;
	font-family: $bodyText;
	font-size: 20px;

	h1{
		font-family: "Princess Sofia";
		font-size: 4em;
		padding: 30px;
	}

	.logo{
		display: flex;
		justify-content: center;
		align-items: center;
		background: $backgroundColor;

		img{
			max-width: 100%;
			max-height: 100%;
		}
	}
}




/* 2. Content ================================= */


#app {
	padding-bottom: 30px;

	ul{
		display: flex;
		justify-content: space-evenly;
	
		.product-card {
			padding: 15px;
			margin: 15px;
			background: white;
			display: flex;
			flex-direction: column;
			width: 30%;
		}

		.active{
			display: block;
			height: auto;
		}

		.btn-more-ingredients{
			width: fit-content;
			padding: 10px;
			justify-content: center; 
			margin: auto;
		}

		h4{
			font-family: $headerText;
			color: $headerColor;
			font-size: 1.2em;
			text-align: center;
			padding-bottom: 10px;
		}

		.links{
			display: flex;
			justify-content: space-around;
			padding-bottom: 20px;
		}

		p{
			padding: 0px 0px 10px;
		}

		a{
			padding: 5px;	
			position: relative;
			display: block;
		}

		.img-size{
			display: flex;
			justify-content: center; 
			align-items: center; 
			width: 220px;
			height: 220px;
			margin: auto;

			img{
				max-width: 100%;
				max-height: 100%;
			}
		}

		small{
			padding: 5px 0px;
			font-size: 12px;
			font-family: verdana;
		}
	}

	span{
		text-decoration: underline;
	}


	p{
		padding: .75em 30px;
	}

	

	h2{
		font-family: $headerText;
		font-size: 3em;
		padding: 30px;
	}

	h3{
		font-family: $headerText;
		color: $headerColor;
		font-size: 2em;
		padding: 10px 30px 0px;
	}

	.bold{
		font-weight: 900;
	}

	.fade-enter-active, .fade-leave-active {
		transition-property: opacity;
		transition-duration: .25s;
	}

	.fade-enter-active {
		transition-delay: .25s;
	}
	
	.fade-enter, .fade-leave-active {
		opacity: 0
	}

	.options{
		display: flex;
		justify-content: space-around;
	}

	.select-style {
		display: block;
		margin: 0 auto;
		border: 2px solid black;
		width: 85%;
		
		border-radius: 3px;
		overflow: hidden;
		background: transparent;
	

		select {
			padding: 5px 8px;
			width: 100%;
			border: none;
			box-shadow: none;
			background: transparent;
			background-image: none;
			-webkit-appearance: none;
			font-size: 20px;
		}

		select:focus {
			outline: none;
		}
	}

	.next, .previous{
		justify-content: center; 
		align-self: center; 

		&.disabled{
			visibility: hidden;
		}

		a{
			font-size: 5em;
			font-family: "Gloria Hallelujah";
			cursor: pointer;
			text-decoration: none;
			color: black;
		}
	}

	.next:hover{
		animation: ani-move-right 1s .2s;
	}

	.previous:hover{
		animation: ani-move-left 1s .2s;
	}

	.buttons{
		padding-top: 20px;
		width: 85%;
		margin: auto;
		display: flex;
		justify-content: flex-end;
		position: relative;
	}

