	button {
		color:#eb1777;
		background: transparent;
		border-width: 2px;
		border-style: solid;
		border-color: #eb1777;
		position:relative;
		margin: .5em;
		display: inline-block;
		padding: 0.5em 1em;
		transition: all 0.3s ease-in-out;
		text-align:center;
		font-family: "raleway";
		font-weight: bold;
		font-size: 20px;
	
		&:before, &:after {
			content: '';
			display: block;
			position: absolute;
			border-color: #eb1777;
			box-sizing: border-box;
			border-style: solid;
			width: 1em;
			height: 1em;
			transition: all 0.3s ease-in-out;
			cursor: pointer;
		}
	
		&:before {
			top: -6px;
			left: -6px;
			border-width: 2px 0 0 2px;
			z-index: 5;
		}

		&:after {
			bottom: -6px;
			right: -6px;
			border-width: 0 2px 2px 0;
		}
		&:hover:before, &:hover:after {
			width: calc(100% + 12px);
			height: calc(100% + 12px);
			border-color: #eb1777;
		}
		&:hover {
			color :#fff;
			background-color: #eb1777;
			border-color: #eb1777;
		}
	}
	
}

